label{
    color:#fff;
    margin-bottom: 0.6rem;
}

form{
    display: flex;
    flex-direction: column;
    padding: 1rem 1rem;
    margin: auto;
    max-width: 600px;
    
}


form input, form textarea{
    margin-bottom: 1rem;
    padding: 10px 18px;
    font-size: 1.2rem;
    background-color: rgba(255,255,255,0.1);
    border-color:rgba(255,255,255,0.1) ;
    color:#f4f4f4;
    

}
#btn{
    margin: 2rem;
}

.error{
    color:red;
}